  unicode-range: u+0020-04c0; }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Trebuchet MS";
  font-display: swap;
  src: local("Trebuchet-MS-Regular"), local("Trebuchet MS Regular"), local("Trebuchet MS"), url("../fonts/Trebuchet-MS.woff2") format("woff2"), url("../fonts/Trebuchet-MS.woff") format("woff");
  unicode-range: u+0022-0491; }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "TrebuchetMS";
  font-display: swap;
  src: local("Trebuchet-MS-Bold"), local("Trebuchet MS Bold"), url("../fonts/Trebuchet-MS-Bold.woff2") format("woff2"), url("../fonts/Trebuchet-MS-Bold.woff") format("woff");
  unicode-range: u+0022-0491; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%; }

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 360px;
  min-height: 100%;
  padding: 40px 0;
  background-color: #000000;
  background-image: url("../img/pattern.jpg");
  background-size: 140px 140px; }
  @media (min-resolution: 2dppx) {
    .page__body {
      background-image: url("../img/pattern@2x.jpg"); } }
  .page__body::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5) 100%);
    pointer-events: none; }

.page__products {
  position: relative;
  z-index: 1; }

.text--bold {
  font-weight: 700; }

.text--ocean {
  color: #1698d9; }

.container {
  padding: 0 20px; }
  @media (min-width: 768px) {
    .container {
      padding: 0 40px;
      left: 0; } }
  @media (min-width: 1280px) {
    .container {
      max-width: 1440px;
      margin: 0 auto;
      padding: 0 80px; } }

.products__slogan {
  margin: 0 0 24px;
  font-weight: 100;
  font-size: 36px;
  line-height: 44px;
  font-family: "Exo 2.0", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 1px #000000; }

.products__list-wrap {
  max-width: 320px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .products__list-wrap {
      max-width: 720px; } }
  @media (min-width: 1280px) {
    .products__list-wrap {
      max-width: none; } }

.products__list {
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (min-width: 768px) {
    .products__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin: -20px; } }
  @media (min-width: 1280px) {
    .products__list {
      justify-content: flex-start;
      margin: -40px; } }

.products__item {
  margin: 0 0 20px; }
  @media (min-width: 768px) {
    .products__item {
      margin: 20px; } }
  @media (min-width: 1280px) {
    .products__item {
      margin: 40px; } }

.card {
  display: flex;
  flex-direction: column;
  width: 320px;
  font-family: "Trebuchet MS", "Arial", sans-serif; }

.card--selected .card__main-content::before {
  border-color: #d91667; }

.card--selected .card__main-content::after {
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 29.40559px, #d91667 29.40559px, #d91667 33.40559px, #f2f2f2 33.40559px); }

.card--selected .card__main-content .card__inner {
  border-color: #d91667; }

.card--selected .card__main-content .card__weight {
  background-color: #d91667; }

.card--selected-hover .card__main-content::before {
  border-color: #e52e7a; }

.card--selected-hover .card__main-content::after {
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 29.40559px, #e52e7a 29.40559px, #e52e7a 33.40559px, #f2f2f2 33.40559px); }

.card--selected-hover .card__main-content .card__inner {
  border-color: #e52e7a; }

.card--selected-hover .card__main-content .card__weight {
  background-color: #e52e7a; }

.card--selected-hover .card__main-content .card__note {
  color: #e52e7a; }

.card--disabled .card__main-content::before {
  border-color: #b3b3b3; }

.card--disabled .card__main-content::after {
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 29.40559px, #b3b3b3 29.40559px, #b3b3b3 33.40559px, #f2f2f2 33.40559px); }

.card--disabled .card__main-content:hover {
  cursor: default; }

.card--disabled .card__main-content .card__inner {
  border-color: #b3b3b3; }

.card--disabled .card__main-content .card__title,
.card--disabled .card__main-content .card__note,
.card--disabled .card__main-content .card__features {
  color: #b3b3b3;
  opacity: 0.5; }

.card--disabled .card__main-content .card__img {
  opacity: 0.5; }

.card--disabled .card__main-content .card__weight {
  background-color: #b3b3b3; }

.card--disabled .card__extra-content {
  color: #ffff66; }

.card:not(.card--selected):not(.card--selected-hover):not(.card--disabled) .card__main-content:hover::before {
  border-color: #2ea8e6; }

.card:not(.card--selected):not(.card--selected-hover):not(.card--disabled) .card__main-content:hover::after {
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 29.40559px, #2ea8e6 29.40559px, #2ea8e6 33.40559px, #f2f2f2 33.40559px); }

.card:not(.card--selected):not(.card--selected-hover):not(.card--disabled) .card__main-content:hover .card__inner {
  border-color: #2ea8e6; }

.card:not(.card--selected):not(.card--selected-hover):not(.card--disabled) .card__main-content:hover .card__weight {
  background-color: #2ea8e6; }

.card__main-content {
  position: relative;
  display: flex;
  flex-grow: 1;
  justify-content: flex-start;
  margin-bottom: 14px; }
  .card__main-content::before {
    content: "";
    flex-shrink: 0;
    width: 43px;
    margin-top: 43px;
    background-color: #f2f2f2;
    border: 4px solid #1698d9;
    border-top: none;
    border-right: none;
    border-radius: 0 0 0 12px; }
  .card__main-content::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 43px;
    height: 43px;
    background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 29.40559px, #1698d9 29.40559px, #1698d9 33.40559px, #f2f2f2 33.40559px); }

.card__inner {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  min-height: 480px;
  padding: 17px 43px 272px 0;
  background-color: #f2f2f2;
  border: 4px solid #1698d9;
  border-left: none;
  border-radius: 0 12px 12px 0; }

.card__title {
  margin: 0 0 15px 5px;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #000000; }

.card__taste {
  display: block;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px; }

.card__note {
  order: -1;
  margin: 0 0 5px 8px;
  font-size: 16px;
  line-height: 19px;
  color: #666666; }
  .card__note--selected {
    display: none; }

.card__features {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: 8px;
  font-size: 14px;
  line-height: 16px;
  color: #666666; }

.card__weight {
  position: absolute;
  right: 16px;
  bottom: 16px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-width: 80px;
  height: 80px;
  margin: 0;
  color: #ffffff;
  background-color: #1698d9;
  border-radius: 50%; }

.card__weight-number {
  font-size: 42px; }

.card__weight-text {
  font-size: 21px;
  line-height: 22px; }

.card__img {
  position: absolute;
  bottom: 4px;
  left: 4px;
  width: 320px;
  height: 272px;
  vertical-align: top;
  border-bottom-left-radius: 7px; }

.card__extra-content {
  min-height: 16px;
  margin: 0;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffffff; }

.card__link-wrap--hidden {
  display: none; }

.card__link {
  display: inline-block;
  color: #1698d9;
  border-bottom: 1px dashed #1698d9; }
  .card__link:hover {
    color: #2ea8e6;
    border-color: #2ea8e6; }
